@charset "UTF-8";

body {
  background: #edf0f1;
  padding: 80px 0 0 0;
}

body,
input,
button {
  font-family: "Roboto", sans-serif;
}
.noFill {
  fill: none;
}

header {
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  padding: 15px;
  z-index: 5;
  background: #25b99a;
  box-shadow: 0p 2px 4px rgba(44, 62, 80, 0.15);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}

header input {
  width: 100%;
  height: 50px;
  float: left;
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  text-indent: 20px;
  padding: 0 60px 0 0;
  background: rgba(255, 255, 255, 0.2);

  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;

  border: 0px;
  box-shadow: none;
  outline: none;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

header input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.75);
}

header button {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 2;
  border-radius: 25px;
  background: #fff;
  border: 0px;
  box-shadow: none;
  outline: none;
  cursor: pointer;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

header button i {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 24%;
  left: 24%;
  margin: 0 auto;
  font-size: 28px !important;
  color: #25b99a;
}

.container {
  width: 100%;
  float: left;
  padding: 15px;
}

ul.todo {
  width: 100%;
  float: left;
}

ul.todo li {
  width: 100%;
  min-height: 50px;
  float: left;
  background: #fff;
  position: relative;
  font-size: 14px;
  font-weight: 500;
  color: #444;
  line-height: 22px;

  border-radius: 5px;
  box-shadow: 0p 1px 2px rgba(44, 62, 80, 0.1);
  margin: 0 0 10px 0;
  padding: 14px 100px 0 14px;
}

ul.todo li:last-of-type {
  margin: 0;
}

ul.todo li .buttons {
  width: 100px;
  height: 50px;
  position: absolute;
  top: 0px;
  right: 0px;
}

ul.todo li .buttons button {
  width: 50px;
  height: 50px;
  float: left;
  background: none;
  border: 0px;
  box-shadow: none;
  outline: none;
  position: relative;
  cursor: pointer;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

ul.todo li .buttons button:last-of-type:before {
  content: "";
  width: 1px;
  height: 30px;
  position: absolute;
  top: 10px;
  left: 0;
  background: #edf0f1;
}

ul.todo li .buttons button i {
  width: 22px;
  height: 22px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -11px 0 0 -11px;
  font-size: 20px !important;
}

ul.todo li .buttons button.complete i {
  border-radius: 11px;
  border: 1.5px solid #25b99a;
  color: #25b99a;
  -webkit-transition: background 0.2s ease;
  -moz-transition: background 0.2s ease;
  transition: background 0.2s ease;
}
ul.todo li .buttons button.remove i {
  color: gray;
  -webkit-transition: color 0.2s ease;
  -moz-transition: color 0.2s ease;
  transition: color 0.2s ease;
}
ul.todo:not(#remove) li .buttons button.remove:hover i {
  color: red;
}

ul.todo#completed li .buttons button.complete i {
  background: #25b99a;
  color: white;
  border: 0px;
}

ul.todo:not(#completed) li .buttons button.complete:hover i {
  background: rgba(37, 185, 154, 0.75);
  color: white;
}

ul.todo#completed:not(:empty) {
  position: relative;
  padding: 60px 0 0 0;
}

ul.todo#completed:not(:empty):before {
  content: "";
  width: 150px;
  height: 1px;
  background: #d8e5e8;
  position: absolute;
  top: 30px;
  left: 50%;
  margin: 0 0 0 -75px;
}

ul.todo#completed:not(:empty) li {
  opacity: 0.5;
}
